import React, { useEffect, useState } from 'react';
import styles from './Gallery.module.css';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

const Gallery: React.FC = () => {
    const images = Array.from({ length: 10 }, (_, index) => `gallery-${index + 1}.jpg`);
    const [photoIndex, setPhotoIndex] = useState(-1);
    const [isOpen, setIsOpen] = useState(false);
    const [loaded, setLoaded] = useState(false);

    const handleOpenLightbox = (index: number) => {
        if (!isOpen) {
            setPhotoIndex(index);
            setIsOpen(true);
        }
    };

    return (
        <div className={styles.Gallery}>
            <div style={{ textAlign: 'center', fontSize: '3vw', lineHeight: '10vw' }}>Gallery</div>
            <div className={styles.PhotoContainer}>
                <div className={styles.GridBox}>
                    {images.map((image, index) => (
                        <div key={index} className={styles.gridItem}
                            onClick={() => handleOpenLightbox(index)}
                        >
                            <img src={`images/${image}`} alt={`Gallery Image ${index + 1}`} />
                        </div>
                    ))}
                </div>
            </div>
            {isOpen && (
                <Lightbox
                    mainSrc={`images/${images[photoIndex]}`}
                    nextSrc={`/images/${images[(photoIndex + 1) % images.length]}`}
                    prevSrc={`/images/${images[(photoIndex + images.length - 1) % images.length]}`}
                    enableZoom={false}
                    onImageLoad={() => {
                        setLoaded(true);
                    }}
                    onCloseRequest={() => {
                        setIsOpen(false);
                        setLoaded(false);
                        setPhotoIndex(-1);
                    }}
                    onMovePrevRequest={() => {
                        setPhotoIndex((photoIndex + images.length - 1) % images.length);
                        setLoaded(false);
                    }}
                    onMoveNextRequest={() => {
                        setPhotoIndex((photoIndex + 1) % images.length)
                        setLoaded(false);
                    }}
                />
            )}
        </div>
    );
}

export default Gallery;
