import React, { useState } from 'react';
import styles from './Calendar.module.css';

const CustomCalendar: React.FC = () => {
    const date = new Date(2024, 9, 5);
    const [year, setYear] = useState(date.getFullYear());
    const [month, setMonth] = useState(date.getMonth());

    const renderDayHeaders = () => {
        const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
        return daysOfWeek.map((day) => {
            if (day === 'Sun') {
                return (
                    <div key={`c-${day}`} className={styles.dayHeaderSun}>
                        {day}
                    </div>
                )
            }
            return (
                <div key={`c-${day}`} className={styles.dayHeader}>
                    {day}
                </div>
            )
        });
    };

    const renderDates = (year: number, month: number) => {
        const firstDayOfMonth = new Date(year, month, 1).getDay();
        const daysInMonth = new Date(year, month + 1, 0).getDate();

        const dates = [];

        // 빈 칸 채우기 (이전 월의 일자)
        for (let i = 0; i < firstDayOfMonth; i++) {
            dates.push(<div key={`empty-${i}`} className={styles.emptyCell}></div>);
        }

        // 현재 월의 날짜들
        for (let day = 1; day <= daysInMonth; day++) {
            if (day === 5) {
                dates.push(
                    <div key={`special-${day}`} className={styles.specialDateContainer}>
                        <svg
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 24 24"
                            fill="#B56576"
                            // width="90vw"
                            // height="90vw"
                            className={styles.heartIcon}
                        >
                            <path d="M12 21.35l-1.45-1.32C5.4 15.36 2 12.28 2 8.5 2 5.42 4.42 3 7.5 3c1.74 0 3.41.81 4.5 2.09C13.09 3.81 14.76 3 16.5 3 19.58 3 22 5.42 22 8.5c0 3.78-3.4 6.86-8.55 11.54L12 21.35z" />
                        </svg>
                        <div className={styles.specialDateText}>{day}</div>
                        <div className={styles.timeText}>오후 12:30</div>
                    </div>
                );
            } else if (day === 6 || day === 13 || day === 20 || day === 27) {
                dates.push(
                    <div key={`sunday-${day}`} className={styles.dateCellSun}>
                        {day}
                    </div>
                );
            } else {
                dates.push(
                    <div key={`normalday-${day}`} className={styles.dateCell}>
                        {day}
                    </div>
                );
            }
        };

        return dates;
    };

    return (
        <div className={styles.calendarContainer}>
            <div className={styles.header}>
                {year}년 {month + 1}월 5일 오후 12시 30분
            </div>
            <div key="dayHeaders" className={styles.dayHeaderContainer}>{renderDayHeaders()}</div>
            <div className={styles.dateGrid}>{renderDates(year, month)}</div>
        </div>
    );
};

export default CustomCalendar;
