import React, { useEffect } from 'react';
import styles from './Map.module.css';
import { Button, Divider } from '@mui/material';

const Map: React.FC = () => {

    useEffect(() => {
        const container = document.getElementById('map');
        if (container) {  // container가 null이 아닌지 확인
            const options = {
                center: new kakao.maps.LatLng(37.2935276397531, 127.05123587041953), // 시은소교회 좌표
                level: 5
            };

            const map = new kakao.maps.Map(container, options);

            const markerPosition = new kakao.maps.LatLng(37.2935276397531, 127.05123587041953);
            const marker = new kakao.maps.Marker({
                position: markerPosition
            });

            marker.setMap(map);
        }
    }, []);

    return (
        <div className={styles.Map}>
            <div style={{ textAlign: 'center', fontSize: '3vw', lineHeight: '10vw' }}>Location</div>
            <div style={{ textAlign: 'center', fontSize: '4vw', fontWeight: '600', lineHeight: '10vw' }}>오시는길</div>
            <Divider orientation="horizontal" variant="middle" sx={{ width: '90vw', marginTop: '2px', borderStyle: 'dashed' }} />
            <div style={{ marginTop: '10%', textAlign: 'center', fontSize: '4vw', fontWeight: '100', lineHeight: '10vw' }}>
                시은소교회 2층 본당
            </div>
            <div style={{ textAlign: 'center', fontSize: '4vw', fontWeight: '100', lineHeight: '10vw' }}>
                경기 수원시 영통구 센트럴타운로 100번길 33
            </div>
            <Button
                variant="outlined" // 외곽선 버튼 스타일
                sx={{
                    display: 'block',
                    margin: '20px auto',
                    borderRadius: '20px',
                    padding: '5px 15px',
                    fontSize: '1rem',
                    color: '#555',
                    borderColor: '#ddd',
                    textTransform: 'none',
                    '&:active': {
                        backgroundColor: 'transparent', // active 시 배경색 변화 없음
                        borderColor: '#ddd', // active 시 테두리 색 변화 없음
                    },
                }}

            >
                <a href="https://map.kakao.com/link/to/시은소교회,37.2935276397531,127.05123587041953" target="_blank" rel="noopener noreferrer" style={{ color: 'inherit', textDecoration: 'none' }}>
                    지도
                </a>
            </Button>
            <div id="map" style={{ width: '100%', height: '50vw', marginTop: '10%' }}></div>
        </div>
    );
}

export default Map;
