import React, { useEffect, useMemo, useState } from 'react';
import styles from './Calendar.module.css';
import './ReactCalendar.css';
import CustomCalendar from './CustomCalendar';

const Calendar: React.FC = () => {
    const date = new Date(2024, 9, 5);
    const calculateDDay = (targetDateString: string): string[] => {
        const now = new Date();
        const nowKST = new Date(now.getTime());

        const targetDate = new Date(targetDateString);

        const diff = targetDate.getTime() - nowKST.getTime();
        const days = Math.floor(diff / (1000 * 60 * 60 * 24)).toString();
        const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)).toString();
        const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60)).toString();

        return [days, hours, minutes];
    }

    // const targetDateString = '2024-03-25T22:00:00+09:00'; // ISO 8601 확장 형식
    const targetDateString = '2024-10-05T12:00:00+09:00'; // ISO 8601 확장 형식
    const curdday = useMemo(() => calculateDDay(targetDateString), [targetDateString]);
    const [dday, setDday] = useState<string[]>(curdday);

    useEffect(() => {
        // 1분마다 dday 상태 업데이트
        const intervalId = setInterval(() => {
            setDday(calculateDDay(targetDateString));
        }, 60000); // 60000ms = 1분

        // 컴포넌트가 언마운트될 때 타이머 정리
        return () => clearInterval(intervalId);
    }, [targetDateString]);


    return (
        <div className={styles.Calendar}>
            <div style={{ textAlign: 'center', fontSize: '3vw', lineHeight: '10vw' }}>Calendar</div>
            <div style={{ width: '100%', margin: '0 auto', textAlign: 'center', display: 'flex', justifyContent: 'center' }}>
                <CustomCalendar />
            </div>
            <div className={styles.ddaybox} style={{ textAlign: 'center', fontSize: '3.5vw', lineHeight: '10vw' }}>
                승태&nbsp;<span style={{ color: '#D099A1' }}>♥</span>&nbsp;영은의 결혼식이
                <div style={{ color: '#D099A1' }}>
                    <div>&nbsp;{dday[0]}일</div>
                    {/* <div>&nbsp;{dday[0]}일&nbsp;{dday[1]}시간&nbsp;</div> */}
                    {/* <div>&nbsp;{dday[0]}일&nbsp;{dday[1]}시간&nbsp;{dday[2]}분</div> */}
                </div> 남았습니다.
            </div>

        </div >
    );
}

export default Calendar;
